"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Textarea } from "@/components/ui/textarea"

export default function Orders() {
  const [formData, setFormData] = useState({
    orderId: "",
    customerName: "",
    quantity: "",
    deliveryDate: "",
    specifications: "",
    status: ""
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // Handle form submission
    console.log(formData)
  }

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
  }

  // Sample orders data
  const ordersData = [
    {
      id: "ORD001",
      date: "2024-01-20",
      customer: "Rahul Construction",
      quantity: 10000,
      deliveryDate: "2024-02-01",
      status: "In Progress"
    },
    {
      id: "ORD002",
      date: "2024-01-19",
      customer: "SK Builders",
      quantity: 5000,
      deliveryDate: "2024-01-25",
      status: "Pending"
    },
    {
      id: "ORD003",
      date: "2024-01-18",
      customer: "Metro Projects",
      quantity: 15000,
      deliveryDate: "2024-02-10",
      status: "Completed"
    }
  ]

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-4xl font-bold text-gray-900">Order Management</h1>
        <p className="text-gray-600 mt-2">Track and manage customer orders</p>
      </div>

      {/* New Order Form */}
      <Card className="p-6">
        <h2 className="text-xl font-semibold mb-4">New Order Entry</h2>
        <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Order ID</label>
            <Input
              name="orderId"
              value={formData.orderId}
              onChange={handleChange}
              placeholder="Enter order ID"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Customer Name</label>
            <Input
              name="customerName"
              value={formData.customerName}
              onChange={handleChange}
              placeholder="Enter customer name"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Quantity (pieces)</label>
            <Input
              name="quantity"
              type="number"
              value={formData.quantity}
              onChange={handleChange}
              placeholder="Enter quantity"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Delivery Date</label>
            <Input
              name="deliveryDate"
              type="date"
              value={formData.deliveryDate}
              onChange={handleChange}
              required
            />
          </div>
          
          <div className="space-y-2 md:col-span-2">
            <label className="text-sm font-medium text-gray-700">Specifications</label>
            <Textarea
              name="specifications"
              value={formData.specifications}
              onChange={handleChange}
              placeholder="Enter order specifications"
              rows={3}
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Status</label>
            <Input
              name="status"
              value={formData.status}
              onChange={handleChange}
              placeholder="Pending/In Progress/Completed"
              required
            />
          </div>

          <div className="md:col-span-2">
            <Button type="submit" className="w-full">Create Order</Button>
          </div>
        </form>
      </Card>

      {/* Orders Table */}
      <Card className="p-6">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-semibold">Orders History</h2>
          <div className="flex space-x-2">
            <Button variant="outline">Filter</Button>
            <Button variant="outline">Export</Button>
          </div>
        </div>
        
        <div className="rounded-md border">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Order ID</TableHead>
                <TableHead>Date</TableHead>
                <TableHead>Customer</TableHead>
                <TableHead>Quantity</TableHead>
                <TableHead>Delivery Date</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {ordersData.map((order) => (
                <TableRow key={order.id}>
                  <TableCell className="font-medium">{order.id}</TableCell>
                  <TableCell>{order.date}</TableCell>
                  <TableCell>{order.customer}</TableCell>
                  <TableCell>{order.quantity}</TableCell>
                  <TableCell>{order.deliveryDate}</TableCell>
                  <TableCell>
                    <span className={`px-2 py-1 rounded-full text-sm ${
                      order.status === "Completed" 
                        ? "bg-green-50 text-green-500"
                        : order.status === "In Progress"
                        ? "bg-blue-50 text-blue-500"
                        : "bg-yellow-50 text-yellow-500"
                    }`}>
                      {order.status}
                    </span>
                  </TableCell>
                  <TableCell>
                    <div className="flex space-x-2">
                      <Button variant="outline" size="sm">Edit</Button>
                      <Button variant="destructive" size="sm">Delete</Button>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </Card>
    </div>
  )
}
